<template><section><html><head></head><body><h1>PointPrimitive</h1>
<p><code v-pre="">vc-primitive-point</code> 组件用于加载点图元，只能作为 <code v-pre="">vc-collection-primitive-point</code> 的子组件使用。</p>
<h2>示例</h2>
<h3>添加点图元到场景</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-collection-primitive-point>
          <template v-for="(polyline, index) of polylines">
            <template v-for="(position, subIndex) of polyline.positions">
              <vc-primitive-point :position="position" :key="'point' + index + 'position' + subIndex" :color="colorPoint" :pixel-size="8" @click="clicked" :disable-depth-test-distance="Number.POSITIVE_INFINITY"></vc-primitive-point>
            </template>
          </template>
        </vc-collection-primitive-point>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
