<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>color</td>
<td>Object</td>
<td>Array</td>
<td>String</td>
</tr>
<tr>
<td>disableDepthTestDistance</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> 指定点深度检测距离。</td>
</tr>
<tr>
<td>distanceDisplayCondition</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定点显示条件。. <strong>结构: { near: number, far: number }</strong></td>
</tr>
<tr>
<td>id</td>
<td>*</td>
<td></td>
<td><code v-pre="">optional</code> 指定与点关联的信息。</td>
</tr>
<tr>
<td>outlineColor</td>
<td>Object</td>
<td>Array</td>
<td>String</td>
</tr>
<tr>
<td>outlineWidth</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> 指定点的轮廓宽度。</td>
</tr>
<tr>
<td>pixelSize</td>
<td>Number</td>
<td><code v-pre="">1</code></td>
<td><code v-pre="">optional</code> 指定点的像素大小。</td>
</tr>
<tr>
<td>position</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定点的位置。 <strong>结构：{ lng: number, lat: number, height: number }</strong></td>
</tr>
<tr>
<td>scaleByDistance</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定点的缩放参数。 <strong>结构： { near: number, nearValue: number, far: number, farValue: number }</strong></td>
</tr>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code>指定点是否显示。</td>
</tr>
<tr>
<td>translucencyByDistance</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定点透明度改变参数。 <strong>结构： { near: number, nearValue: number, far: number, farValue: number }</strong></td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>参考官方文档：<strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/PointPrimitive.html">PointPrimitive</a></strong></li>
</ul>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>mousedown</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标在该图元上按下时触发。</td>
</tr>
<tr>
<td>mouseup</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标在该图元上弹起时触发。</td>
</tr>
<tr>
<td>click</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标单击该图元时触发。</td>
</tr>
<tr>
<td>clickout</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标单击该图元外部时触。</td>
</tr>
<tr>
<td>dblclick</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标左键双击该图元时触发。</td>
</tr>
<tr>
<td>mousemove</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标在该图元上移动时触发。</td>
</tr>
<tr>
<td>mouseover</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标移动到该图元时触发。</td>
</tr>
<tr>
<td>mouseout</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标移出该图元时触发。</td>
</tr>
</tbody>
</table>
<hr>
